	module.exports = {"data":{"page":{"id":"C:/dasGatsblog/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-8479d.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.23376623376623%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"unsplash.com\"\n        title=\"\"\n        src=\"/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-78f2b.jpg\"\n        srcset=\"/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-dce19.jpg 200w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-c1413.jpg 400w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-78f2b.jpg 800w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-ab4c4.jpg 1200w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-bc99b.jpg 1600w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-34a60.jpg 2400w,\n/static/aboutPic-08d9be1636ec1583cf14620dc1e5b426-8479d.jpg 4928w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Currently I am working as a .Net Full Stack Developer with a passion to improve technology in order to achieve excellence.\nI have a Bachelors in Computer Science and have pursued knowledge of technology all my life and love to automate processes and creatively solve problems.</p>\n<h2>Experience</h2>\n<h3>Systems Analyst/Web Developer at United States Department of Defense</h3>\n<p><em>August 2010 - Present (7 years+)</em></p>\n<p>I am Microsoft .Net Full Stack applications developer for G-6 in AMEDD Medical School at Fort Sam\nHouston. </p>\n<p>Technologies used: Asp.Net 4.5, React.js 16.0, Web Api 1.0, Powershell 5.0, C# 7.0, VB.Net 15.0, Visual Studio 2017, VS Code 1.22.1, SQL Server 2012, Node 9.5.0, </p>\n<h3>Computer Technician at Decypher</h3>\n<p><em>November 2009 - August 2010 (10 months)</em></p>\n<p>Manage over 600 computers and troubleshoot any computer issues, hardware and software, inside multiple OU’s(Organizational\nUnits).</p>\n<h3>Web Developer at Decypher Technologies</h3>\n<p><em>November 2008 - November 2009 (1 year)</em></p>\n<p>Developed web site applications and maintained several web applications using Visual Team Studio 2008 and\nSQL Server 2005.\nProgramming languages/Skills: ASP.Net 3.0, SQL, AJAX, SQL Server 2005, VBScript, JavaScript, Windows\nXP and Vista, Office Suite 2003 &#x26; 2007.</p>\n<h3>Application Programmer at Accenture / Sapphire Technologies</h3>\n<p><em>May 2008 - October 2008 (6 months)</em></p>\n<p>Developed an automated solution using Microsoft Access and outsourced the work to India and was part of\na project that won the 2008 Best Idea of the Year award for automating processes and achieved annualized\nsavings of over $500,000.\nMaintained an automated web reporting</p>\n<h3>Helpdesk Technician/ Administrator at TEKsystems / USAA Helpdesk</h3>\n<p><em>February 2007 - May 2008 (1 year 3 months)</em></p>\n<p>Provided internal IT support to over 12,000 employees and over 120 applications.\nPart of scripting team which developed and maintained applications and scripts using Visual Basic.NET\ninside Visual Studio 2003 to better assist the help desk.\nProvided reports and automate processes using Access, SQL and Excel.</p>\n<h3>Earthlink Tier II - Technical Support Representative at West Corporation / Earthlink</h3>\n<p><em>June 2006 - February 2007 (9 months)</em></p>\n<p>Provided Technical Support for Earthlink customers for Dial-up, Broadband and DSL communications.</p>\n<h3>Maintenance Data Analyst at United States Air Force</h3>\n<p><em>June 1995 - June 2003 (8 years)</em></p>\n<p><strong>Database Manager</strong> – Performed, briefed Statistical Analysis to higher personnel and maintained, repaired the\nCAMS (Core Automated Maintenance System) as a database manager.\nStatistical Analyst - Supported maintenance personnel by providing consistent Analysis of all Aircraft data.\nComputed Statistical Analysis and briefed providing a management tool, saving the Air Force valuable\nresources and money.</p>\n<p><strong>Automation Expert</strong> - Stream-lined products using Visual Basic to connect multiple Microsoft Access, SQL\nServer databases web-enabling them using Active Server Pages (ASP), significantly decreasing processing\ntime and increasing accuracy.</p>\n<p><strong>Computer Technician</strong> - Provided IT support on variety of IT tasks to include hardware fixes and software troubleshooting.</p>","frontmatter":{"title":"About"}},"footnote":{"id":"C:/dasGatsblog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>home of the <a href=\"https://github.com/376888/\">personal Tech Blog of Paul B. Castillo</a></li>\n<li>built by <a href=\"http://paulbcastillo.me\">Paul B. Castillo</a></li>\n<li>GatsbyJS, ReactJs, CSS in JS</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"..."}}}},"pathContext":{"slug":"/about/"}}

